<template>
    <div class="communication-setdialog">
        <el-form :model="form" label-width="120px" :rules="rules" ref="comForm">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="" prop="macNo">
                        <el-select size="mini" v-model="form.macNo">
                            <el-option v-for="item in macNoList" :value="item.value" :label="item.name"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="keyType">
                        <el-input size="mini" v-model="form.keyType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="isYield">
                        <el-select size="mini" v-model="form.isYield">
                            <el-option :value=1 label=""></el-option>
                            <el-option :value=0 label=""></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="/" prop="direction">
                        <el-select size="mini" v-model="form.direction">
                            <el-option :value=1 label=""></el-option>
                            <el-option :value=0 label=""></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList"],
    data() {
        return {
            rules: {
                macNo: [
                    { required: true, message: '', trigger: 'change'}
                ],
                keyType: [
                    { required: true, message: '', trigger: 'blur'}
                ],
                isYield: [
                    { required: true, message: '', trigger: 'change'}
                ],
                direction: [
                    { required: true, message: '/', trigger: 'change'}
                ],
            }
        }
    }
}
</script>
<style lang="less">
    .communication-setdialog {

    }
</style>



